.hero {
	position: relative;
	padding-top: 55px;
	&:before {
		content: '';
		position: absolute;
		top: 67%;
		width: 550%;
		height: 153%;
		background-color: $darkblue;
		transform: rotate(30deg);
		transform-origin: center center;
		left: 50%;
		transform: translateX(-50%) rotate(30deg);
		padding: 35px 0 80px; }
	&__cube-small {
		position: absolute;
		left: 7%;
		top: 16%; }
	&__cube-blur {
		position: absolute;
		right: 10%;
		top: 22%;
		z-index: 1; }
	&__top {
		position: relative;
		@include clr; }
	&__img {
		position: absolute;
		top: -104.5%;
		left: -5%;
		z-index: 2;
		@include md {
			max-width: 802px;
			top: -76%;
			left: -4%;
			img {
				@include md {
					display: block;
					max-width: 100%; } } }
		@include sm {
			display: none;
			img {
				display: block;
				max-width: 100%; } } }
	&__img-mob {
		position: absolute;
		left: 0;
		top: -59.7%;
		display: none;
		z-index: 2;
		@include sm {
			display: block; } }
	&__info {
		float: right;
		position: relative;
		max-width: 600px;
		text-align: center; }
	&__title {
		margin-bottom: 13px; }
	&__button {
		margin-top: 25px; }
	&__in-lg {
		margin-top: 211px;
		position: relative; } }

.video {
	position: relative;
	&__content {
		width: 100%;
		height: 725px;
		background: $black;
		border: 50px solid $white;
		border-radius: 16px; }
	&__btn {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		.icon-play {
			fill: #00bdca;
			font-size: 123px; }
		&:before {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			@include arr(40,40, $white, r); } } }
