.header {
	padding: 63px 0;
	position: relative;
	z-index: 3;
	@include sm {
		padding: 24px 0; }
	&__wrap {
		@include clr; }
	&__left {
		float: left;
		display: flex;
		flex-wrap: wrap;
		align-items: center; }
	&__right {
		float: right;
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start; }
	&__logo {
		margin-right: 10px;
		.icon-logo {
			fill: $white;
			font-size: 45px;
			@include md {
				font-size: 35px; } } }
	&__menu {
		margin-right: 30px;
		@include sm {
			position: absolute;
			opacity: 0;
			// left: 0
			right: 0;
			width: 200px;
			text-align: center;
			// height: 100vh
			transform: translateX(200px);
			visibility: hidden;
			background: $blue;
			margin-right: 0;
			top: 20px;
			transition: opacity 0.5s, transform 0.5s; }
		.is-open & {
			opacity: 1;
			visibility: visible;
			transform: translateY(0); } }


	&__button {
		margin-right: 8px;
		@include sm {
			display: none; } }
	&__logo-text {
		font-size: 24px;
		font-weight: 600;
		color: $white;
		line-height: 1.2;
		@include md {
			font-size: 18px; }
		&_small {
			font-size: 13px;
			font-weight: 500;
			@include md {
				font-size: 10px; } } }
	&__logo-blue {
		color: $malibu; }
	&__lang {} }

.menu {
	font-size: 0;
	display: inline-block;
	@include sm {
		padding: 10px 20px; }
	&__item {
		display: inline-block;
		padding: 15px 0;
		&:not(:last-child) {
			margin-right: 35px;
			@include md {
				margin-right: 20px; }
			@include sm {
				display: block;
				margin-right: 0; } } }
	&__link {
		color: $white;
		display: block;
		font-size: 16px;
		font-weight: 600;
		text-decoration: none;
		font-family: $montserrat;
		@include md {
			font-size: 14px; } } }

