body {
	font-family: $montserrat;
	&.is-open {
		overflow: hidden; } }

.out {
	background-color: $blue;
	overflow: hidden; }

.in {
	max-width: 1002px;
	padding: 0 15px;
	margin: 0 auto;
	position: relative; }

.in-lg {
	max-width: 1230px;
	margin: 0 auto;
	padding: 0 30px;
	position: relative;
	@include sm {
		padding: 0 5px; } }

.btn-nav {
	position: absolute;
	display: none;
	top: 5px;
	right: 15px;
	width: 16px;
	height: 16px;
	background-color: transparent;
	@include sm {
		display: block; }
	.header.is-open & {
		span {
			&:nth-child(1) {
				transform: rotate(45deg);
				top: 5px; }
			&:nth-child(2) {
				opacity: 0; }
			&:nth-child(3) {
				transform: rotate(-45deg);
				top: 5px; } } }
	span {
		position: absolute;
		left: 0;
		background: $white;
		width: 16px;
		height: 2px;
		transition: transform 0.3s;
		&:nth-child(1) {
			top: 0; }
		&:nth-child(2) {
			top: 5px; }
		&:nth-child(3) {
			top: 10px; } } }

.btn {
	border-radius: 16px;
	background-color: $white;
	color: $darkblue;
	padding: 14px 30px;
	font-size: 16px;
	font-weight: 600;
	@include md {
		font-size: 14px;
		padding: 10px 22px; } }

.lang {
	border-radius: 20px;
	// background: $white
	font-size: 16px;
	font-weight: 600;
	padding: 15px 25px;
	@include md {
		font-size: 14px; }
	&__title {
		color: $white;
		text-decoration: none; }
	&__list {
		display: none; }
	&__link {
		display: inline-block;
		text-decoration: none;
		// color: $darkblue
		color: $white;
		border-bottom: 1px solid transparent;
		&:hover {
			border-bottom: 1px solid $darkblue; } } }

.title-lg {
	font-size: 72px;
	font-weight: 600;
	color: $white;
	line-height: 1.2;
	@include md {
		font-size: 48px; }
	&_darken {
		color: $darkblue; }
	span {
		font-weight: 200; } }

.title-md {
	font-size: 60px;
	font-weight: 600;
	color: $white;
	line-height: 1.2;
	&_darken {
		color: $darkblue; }
	@include sm {
		font-size: 36px;
		text-align: center; } }

.title-sm {
	font-size: 30px;
	line-height: 1.5;
	font-weight: 600;
	color: $white; }

.title-xs {
	font-size: 24px;
	font-weight: 600;
	color: $white;
	line-height: 1.5; }

.button {
	border-radius: 40px;
	background-color: $darkblue;
	color: $white;
	font-size: 16px;
	font-weight: 600;
	padding: 21px 20px;
	min-width: 320px;
	font-family: $montserrat;
	&__value {
		margin-left: 11px;
		display: inline-block;
		vertical-align: middle;
		font-size: 18px; }
	&__icon {
		display: inline-block;
		vertical-align: middle; }
	&_big {
		min-width: 374px; }
	&_white {
		background-color: $white;
		color: $darkblue; }
	.icon-paper-plane {
		font-size: 23px; } }

.col {
	float: left;
	padding: 0 30px;
	@include sm {
		padding: 0 10px; }
	&-50 {
		width: 50%; }
	&-33 {
		width: 33.3%; }
	// &-lg-25
	// 	+lg
	// 		width: 25%
	&-md-33 {
		@include md {
			width: 33%; } }
	&-md-50 {
		@include md {
			width: 50%; } }
	&-sm-33 {
		@include sm {
			width: 33.3%; } }
	&-sm-50 {
		@include sm {
			width: 50%; } }
	&-sm-100 {
		@include sm {
			width: 100%; } } }
